import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { inject, observer } from "mobx-react";
import axios from 'axios';
import lodash from 'lodash';

@inject("userStore")
@observer
export default class Login extends Component {
	state = {email: '', password: '', error: {}, loading: false };
	login = (event) => {
		event.preventDefault();
		this.setState({loading: true});
		let {email, password} = this.state;
		axios.post('/auth/login',{email, password}).then(res=>{
			this.props.userStore.setUser(res.data);
		},error=>{
			error = error.response ? error.response.data : error;
			this.setState({error});
		}).finally(()=>this.setState({loading: false}));
	}

	emailChanged = (event) => {
		this.setState({email: event.target.value});
	}

	passwordChanged = (event) => {
		this.setState({password: event.target.value});
	}

	render() {
		let error = this.state.error;
		let emailIsInvalid = error.errors && error.errors.email ? 'is-invalid' : '';
		let passwordIsInvalid = error.errors && error.errors.password ? 'is-invalid' : '';
		return <form id="login-form" onSubmit={this.login}>
							<div className="col-md-4 col-sm-6 ml-auto mr-auto">
								<div className="card card-login">
									<div className="card-header ">
										<img src="/img/logo.jpg" style={{display: 'block', marginLeft: 'auto', marginRight: 'auto', width: '50%'}} />
									</div>
									<div className="card-body">
										{error.message && <div className="alert alert-danger">
                        <span>
                            <b> Error - </b> {error.message}
                        </span>
                    </div>}
										<div className="form-group">
	                    <label>Email</label>
	                    <div className="form-group field-loginform-email required">
												<input value={this.state.email} onChange={this.emailChanged} type="text" id="loginform-email"
													className={`form-control ${emailIsInvalid}`} name="email" placeholder="Email" aria-required="true" />
												{emailIsInvalid && error.errors.email.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
											</div>
										</div>
              			<div className="form-group">
                  		<label>Password</label>
                  		<div className="form-group field-loginform-password required">
												<input value={this.state.password} onChange={this.passwordChanged} type="password" id="loginform-password" className={`form-control ${passwordIsInvalid}`} name="password" placeholder="Password" aria-required="true" />
												{passwordIsInvalid && error.errors.password.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
											</div>
										</div>
		                <div className="form-group">                    
		                  <span> <a href="/site/request-password-reset">Reset your password</a></span>
		                </div>
          				</div>
					        <div className="card-footer ml-auto mr-auto">
					          <button type="submit" disabled={this.state.loading} className="btn btn-warning btn-wd" name="signup-button">Login</button>
					          &nbsp;
					          <Link to="/register" disabled={this.state.loading} className="btn btn-info btn-wd">Register</Link>
					        </div>
  							</div>
  						</div>
						</form>
	}
}