import React, {Component} from 'react';
import {BrowserRouter as Router,Route,Redirect,Link} from 'react-router-dom';
import { inject, observer } from "mobx-react";
import axios from 'axios';

import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import AnimatedSwitch from '../components/animated-switch';

import Login from './Login';
import Register from './Register';

window.axios = axios;

@inject("userStore")
@observer
export default class Auth extends Component {
	render() {
		if(this.props.userStore.user.email){
			let redirect = (this.props.location.state && this.props.location.state.redirect) || '/'
			return <Redirect to={redirect} />;
		}

		let match = this.props.match;

		return <div className="wrapper wrapper-full-page">
			<Navbar match={match} />
			<div className="full-page register-page section-image" data-color="orange" data-image="/theme/img/bg5.jpg">
        <div className="content">
          <div className="container">
						<AnimatedSwitch>
							<Route path="/login"> <Login /> </Route>
							<Route path="/register"> <Register /> </Route>
						</AnimatedSwitch>
					</div>
				</div>
			</div>
      <Footer />
		</div>
	}
};